@import "color";
.login-wrapper {height: 100vh; 
    &>div {max-width: 350px;}
}
form {
    .input-wrap { position: relative;
        &.focused {
            .input-label-text {position: absolute; top: 0; left: 1rem; color: $gray-600; transform: translate(0, -50%);}
        }
        .form-control {font-size: 1rem; padding: 1rem .75rem;}
        .input-label-text {position: absolute; top: 50%; left: 1rem; color: $gray-600; transform: translate(0, -50%); transition: all .3s ease-out; background-color: #fff; z-index: 9999;}
    }
}
.login-btn {background-color: $indigo; color: #fff; font-size: 1.2rem; font-weight: 700;}